            <div className="mt-3 pt-3 border-t" style={{ borderColor: 'var(--info-primary)' }}>
              <p className="text-xs" style={{ color: 'var(--info-dark)' }}>
                üíÅ‚Äç‚ôÄÔ∏è <strong>ÌåÅ:</strong> Ï≤òÏùå ÏÇ¨Ïö©ÌïòÏã†Îã§Î©¥ $5-10 Ï†ïÎèÑÏùò ÏÜåÏï°ÏúºÎ°ú ÏãúÏûëÌï¥Î≥¥ÏÑ∏Ïöî. 
                ÎåÄÎ∂ÄÎ∂ÑÏùò Ïó∞Íµ¨ Î™©Ï†ÅÏúºÎ°úÎäî Ï∂©Î∂ÑÌï©ÎãàÎã§.
              </p>
            </div>
